@use "global" as *;

.header {
    position:fixed;
    top:0;
    left:4vw;
    width:92.05vw;
    margin-left: auto;
    margin-right: auto;
    background-color:rgba(255,255,255,0.8);
    box-shadow: rem(1.4) rem(1.4) rem(1.4) 0px rgba(0, 0, 0, 0.4);
    border-radius: rem(10);
    z-index:99;
    @include mq(md) {
        position: initial;
        width: 100%;
        border-radius: initial;
        background-color: #573B1A;
        padding:0.95vw 0 1.02vw 12.45vw;
    }
}

.header-container {
    padding:3.67vw 5.16vw 4.51vw 4.76vw;
    @include mq(md) {
        display: none;
    }
}

.header-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-logo {
    width:23.59vw;
}

.header-wrap {
    display: flex;
    gap:0 5.05vw;
}

.header-icon {
    width:6.6vw;
}

.header-text {
    color: $white;
    text-align: center;
    display: none;
    @include mq(md) {
        display: block;
        font-size: rem(11.5);
        line-height: calc(11.5 / 11.5);
    span {
        margin-right: rem(33);
        font-size: rem(12.5);
        line-height: calc(12.5 / 12.5);
        }
    }

}