@use "global" as *;

.side {
    display: none;
    @include mq(md) {
        display: block;
        position:fixed;
        top:11.8vw;
        left:3.66vw;
        z-index: 10;
    }
    @include mq(lg) {
        top:8.59vw;
    }
}

.side-top {
    background-color: $white;
    opacity: 0.6;
    @include mq(md) {
        border-radius: rem(10);
        box-shadow: rem(1) rem(1) rem(1) 0px rgba(0, 0, 0, 0.4);
    }
}

.side-logo {
    @include mq(md) {
        width:5.9vw;
        margin-right: 6.02vw;
        margin-left:5.34vw;
        padding:1.41vw 0 1.68vw 0;
    }
}

.side-nav {
    background-color: $white;
    opacity: 0.6;
    @include mq(md) {
        margin-top:0.4vw;
        border-radius: rem(10) rem(10) 0 0;
        padding:1.83vw 0 5.6vw 2.2vw;
        box-shadow: rem(1) rem(1) rem(1) 0px rgba(0, 0, 0, 0.4);
    }
}

.side-menu {
    @include mq(md) {
        max-width:17.26vw;
    }
}

.side-nav__catch {
    color:$accent;
    @include mq(md) {
        font-size:1.32vw;
        line-height: calc(29 / 18);
        font-family: 'accent';
    }
}

.side-nav__text {
    color:$accent;
    @include mq(md) {
        margin-top: rem(10);
        font-size: rem(10);
        line-height: calc(15 / 7.09);
        font-family: 'accent';
        transform: scale(0.73); 
        transform-origin: left top; 
    }
    @include mq(lg) {
        white-space: nowrap;
    }
}

.side-nav__items {
    @include mq(md) {
        margin-top:-2vw;
        padding-left:0.37vw;
    }
}

.side-nav__item a {
    font-family: 'serif';
    @include mq(md) {
        display: flex;
        align-items: center;
        gap:0 0.26vw;
        font-size: rem(14);
        line-height: calc(30 / 14);
        letter-spacing: 0.075em;
    }
}

.side-nav-angle {
    @include mq(md) {
        width: 0.44vw;
    }
}

.side-nav__icon {
    @include mq(md) {
        margin-top:1.1vw;
        width:1.08vw;
    }
}

.side-nav__shop a {
    color:$white;
    @include mq(md) {
        padding:1.28vw 0 1.52vw 0;
        background-color: #603813;
        font-family: 'serif';
        font-size:1.02vw;
        line-height: calc(14 / 14);
        display: flex;
        justify-content: center;
        align-items: baseline;
        gap:0 0.73vw;
        border-radius: 0 0 rem(10) rem(10);
    }
}