@use "global" as *;

.top__about {
    margin-top: 62.57vw;
    @include mq(md) {
        margin-top: rem(150.4);
    }
}

.top-about__tittle {
    color:$white;
}

.top-about__tittle__ja {
    color:$white;
}

.top-about-back-sp {
    background-image: url(../images/top/about\ us.jpg);
    background-size:cover;
    padding-top:19.4vw;
    padding-bottom:18.72vw;
    @include mq(md) {
        background-image: initial;
        padding-top: initial;
        padding-bottom: initial;
    }
}

.top-about-inner {
    display: flex;
    gap:0 9.8vw;
    padding-left:0.07vw;
    padding-right:2.67vw;
    @include mq(md) {
        display: block;
        padding-left: initial;
    }
}

.top-about-container {
    @include mq(md) {
        background-image: url(../images/top/about\ us.jpg);
        background-size:cover;
        padding-top: rem(120);
        padding-bottom: rem(148.5);
    }
}

.top-about-wrapper {
    display: none;
    @include mq(md) {
        display: flex;
        align-items: center;
        gap:0 rem(10);
        padding-right:8.86%;
        white-space: nowrap;
    }
}

.top-about__text {
    color:$black;
    font-family: 'serif';
    font-size: 3.47vw;
    line-height: calc(65 / 26);
    padding-left: 8.13vw;
    padding-right: 1.2vw;
    margin-top: 9.68vw;
    @include mq(md) {
        color:$white;
        margin-top: rem(65);
        font-size: rem(16);
        line-height:calc(49 / 16);
        padding-right:initial;
        padding-left: initial;
    }
}

.top-about__logo {
    width:25.38vw;
    @include mq(md) {
        width:rem(190.36);
        margin-top: rem(-30);
    }
}

.top-about__btn {
    margin-top: 7.19vw;
    margin-left: 8vw;
    margin-right: 8vw;
    @include mq(md) {
        margin-top: rem(32);
        margin-left: initial;
        margin-right: initial;
    }
}