@use "global" as *;

.mv {
  position: relative;
  height:100vh;
  @include mq(md) {
    height:initial;
  }

  .swiper-container {
    height: 100%;
  }

  .swiper-slide,
  .slide-img,
  .slide-img img {
    height: 100%;
    object-fit: cover;
  }
}

  .mv-pagination {
    position: absolute;
    top:31.5vw;
    right:4.5vw;
    @include mq(md) {
        top:rem(6);
        right:rem(15);
    }

    .swiper-pagination-bullet {
        border:rem(1) solid $white;
        background-color: initial;
        width:2.72vw;
        height:2.72vw;
        opacity: 1;
        margin:0 1.55vw;
        @include mq(md) {
          width:rem(8.34);
          height:rem(8.34);
          margin:0 rem(6);
        }
      }

    .swiper-pagination-bullet-active {
        background-color: $white;
    }
  }

  .swiper-wrapper {
    @include mq(md) {
      overflow:hidden;
    }
  }

.mv-scrolldown{
position:absolute;
bottom:16.11vw;
z-index:11;
right:4.19vw;
height:20.36vw;
@include mq(md) {
  bottom:4.9vw;
  right:1.54vw;
}
}

.mv-scrolldown span {
  writing-mode: vertical-rl;
  white-space: nowrap;
  color: $white;
  font-size:2.67vw;
  @include mq(md) {
    position: absolute;
    left:-0.88vw;
    bottom:1.17vw;
    font-size:0.88vw;
    letter-spacing: 0.075em;
  }
}

.mv-scrolldown::after{
content: "";
position: absolute;
bottom:0;
left:1.73vw;
width:0.13vw;
height:13.86vw;
background: $white;
animation: pathmove 2.5s ease-in-out infinite;
opacity:0;
@include mq(md) {
  height:4.25vw;
  left:-0.37vw;
}
}

/*高さ・位置・透過が変化して線が上から下に動く*/
@keyframes pathmove {
0%{
  height:0;
  top:20.4vw;
  opacity: 0;
  @include mq(md) {
    top:4.9vw;
  }
}
30%{
  height:13.86vw;
  opacity: 1;
  @include mq(md) {
    height:4.25vw;
  }
}
100%{
  top:20.4vw;
  opacity: 0;
  height:0;
@include mq(md) {
  top:11.57vw;
}
}
}