@use "global" as *;

.about__inner {
    background-image: url(../images/about/about_mv.png);
    background-size:cover;
}

.page-back-inner .section-tittle {
    position: absolute;
    top:36.03vw;
    left:6.84vw;
    @include mq(md) {
        top:21.76vw;
        left:3.73vw;
    }
}

.page-about__container {
    margin-top: 12vw;
    @include mq(md) {
        margin-top: rem(98);
    }
}

.page-about__text {
    font-family: 'serif';
    font-size: 3.47vw;
    line-height: calc(65 / 26);
    font-weight: $regular;
    padding-right: 3.4vw;
    letter-spacing: -0.035em;
    @include mq(md) {
        padding-left: rem(45);
        font-size: 1.17vw;
        line-height: calc(42 / 16);
        padding-right: initial;
        letter-spacing: initial;
    }
}

.page-about__image {
    margin-top: 17.57vw;
    @include mq(md) {
        margin-top: rem(130);
    }
}

.page-about__history {
    margin-bottom: 16.68vw;
    @include mq(md) {
        position: relative;
        color:$white;
        margin-top: rem(90);
        margin-bottom: rem(216.5);
    }
    .page-about__text {
        margin-top: 8.16vw;
        @include mq(md) {
            padding-left: 0;
            margin-top: initial;
        }
    }
}

.page-about__history__image {
    margin-top: 8.23vw;
    @include mq(md) {
        margin-top: rem(90);
    }
}

.page-about__history__wrap {
    @include mq(md) {
        position:absolute;
        top:17.17vw;
        left:3.37vw;
    }
}

.page-about__btn {
    margin-right: 8vw;
    @include mq(md) {
        margin-top:3.21vw;
        margin-right: initial;
    }
    .ex-link {
        width:3.63vw;
        position: absolute;
        top:50%;
        right:3.48vw;
        transform: translateY(-50%);
        @include mq(md) {
            width:rem(12.59);
            right:0.8vw;
        }
    }
}

.page-about-btn {
    font-family: 'serif';
    margin-top: 7.79vw;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    height:9.22vw;
    font-size: 3.73vw;
    line-height: calc(28 / 28);
    border:0.13vw solid $black;
    @include mq(md) {
        justify-content: space-between;
        height:rem(34.06);
        font-size:1.02vw;
        line-height: calc(20 / 14);
        padding:0 rem(10.9) 0 rem(31.8);
        border:rem(1) solid $white;
        width:22.25vw;
        margin-top: initial;
    }
}