@use "global" as *;

.contact-wrapper {
    margin-right: 8vw;
    border-top: 0.13vw solid #CBCBCB;
    margin-top: 12.27vw;
    padding-top: 9.81vw;
    margin-bottom: 11.28vw;
    @include mq(md) {
        padding-top: rem(47.7);
        margin-top: rem(93.8);
        border-top: rem(1) solid #CBCBCB;
        margin-bottom: rem(137.7);
        margin-right: 8.02vw;
    }
}

.contact__text {
    font-family: 'serif';
    font-size: 3.2vw;
    line-height: calc(47 / 24);
    @include mq(md) {
        font-size: rem(14);
        line-height: calc(27 / 14);
        font-weight: $medium;
    }
}

.contact-form {
    margin-top: 12.97vw;
    @include mq(md) {
        margin-top: rem(62.6);
    }
}

.contact-form__required {
    font-size: 3.47vw;
    line-height: calc(29 / 26);
    font-family: 'serif';
    @include mq(md) {
        font-size: rem(13);
        line-height: calc(19 / 13);
    }
    span {
        color:#EC1C24;
    }
}

.contact__thanks__text {
    font-size: 4.22vw;
    line-height: 1;
    margin-bottom:8.68vw;
    display: block;
    @include mq(md) {
        font-size:1.46vw;
        margin-bottom: 2.49vw;
    }
}

.contact__btn {
    margin-top: 9.27vw;
    @include mq(md) {
        margin-top: 2.44vw;
    }
}

// contact form 7用

.contact_table,
.contact_table tbody,
.contact_table tr,
.contact_table td,
.contact_table p {
    width: 100%;
    display: block;
}

.wpcf7-validates-as-required {
    font-size: 2.67vw;
    line-height: calc(23 / 20);
    font-family: 'serif';
    color:$black;
    width: 100%;
    display: block;
    background-color: #F1F1F1;
    padding:3.97vw 2.67vw 4.24vw 5.55vw;
    margin-top:2.67vw;
    @include mq(md) {
        font-size: 0.73vw;
        line-height: calc(10 / 10);
        padding:1.09vw 1.46vw 1.16vw 1.52vw;
        margin-top: 0.73vw;
    }
}

.wpcf7-list-item-label {
    font-size: 3.73vw;
    margin-left: 3.43vw;
    font-family: 'serif';
    @include mq(md) {
        font-size: 1.02vw;
        margin-left: 0.98vw;
    }
}

.wpcf7-acceptance .wpcf7-list-item {
    position: relative;
    margin: initial;
    margin-top: 8.16vw;
    @include mq(md) {
        margin-top: 2.27vw;
    }
}

.policy {
    position: absolute;
    top:50%;
    left:0;
    transform: translateY(-10%);
    display:inline-flex;
    width:5.56vw;
    height:5.56vw;
    border:0.27vw solid #666;
    background-color:#F1F1F1;
    @include mq(md) {
        width:1.53vw;
        height:1.53vw;
        border:0.15vw solid #666;
    }
}

.policy-link {
    color: #29AAE1;
    border-bottom: 0.13vw solid #29AAE1;
    @include mq(md) {
        border-bottom: 0.07vw solid #29AAE1;
    }
}

/* チェックボックスデザイン */
input[type="checkbox"] {
    cursor: pointer;
    vertical-align: middle;
    position: relative;
  }
  input[type="checkbox"]::before,
  input[type="checkbox"]::after {
    content: "";
    display: block; 
    position: absolute;
  }
  input[type="checkbox"]::after {
    border-bottom: 0.4vw solid $black;/*チェックの太さ*/
    border-left: 0.4vw solid $black;/*チェックの太さ*/
    opacity: 0;/*チェック前は非表示*/
    height: 1.6vw;/*チェックの高さ*/
    width: 2.93vw;/*チェックの横幅*/
    transform: rotate(-45deg);
    top:1.2vw;
    left:1.2vw;
    @include mq(md) {
        border-bottom:0.22vw solid $black;/*チェックの太さ*/
        border-left:0.22vw solid $black;/*チェックの太さ*/
        width:1.1vw;
        height:0.57vw;
        top:0.22vw;
        left:0.12vw;
    }
  }
  input[type="checkbox"]:checked::after {
    opacity: 1;/*チェック後表示*/
  }

  //確認画面

  .confirm__table,
  .confirm__table tbody,
  .confirm__table tr,
  .confirm__table td,
  .confirm__table p {
      width: 100%;
      display: block;
  }

  .confirm__table tr {
    display: block;
  }

  .confirm__table tr:not(:first-child) {
    margin-top: 16.92vw;
    @include mq(md) {
        margin-top: 4.44vw;
    }
  }

  .multiform__text {
    display: block;
    font-family: 'serif';
    font-size: 3.47vw;
    line-height: 1;
    color:$black;
    cursor: initial;
    @include mq(md) {
        font-size: 1.1vw;
    }
    span {
        display: block;
        margin-top: 5.97vw;
        font-size: 4vw;
        @include mq(md) {
            font-size: 1.1vw;
            margin-top: 1.01vw;
        }
    }
  }

  .confirm__btn {
    margin-top: 18.55vw;
    @include mq(md) {
        margin-top: 4.82vw;
    }
  }