@use "global" as *;

.page-news__inner {
    @include mq(md) {
        margin-right: 7.99vw;
    }
}

.page-news__contents {
    margin-top: 12.28vw;
    @include mq(md) {
        margin-top: rem(100);
    }
}

.page-news__list {
    border-top: 0.13vw solid #CBCBCB;
    padding-top: 7.77vw;
    @include mq(md) {
        border-top: rem(1) solid #CBCBCB;
        padding-top: rem(30);
    }
}

.page-news__list:nth-child(n+2) {
    margin-top: 7.75vw;
    @include mq(md) {
        margin-top: rem(90);
    }
}

.page-news__image img {
    height: 100%;
}

.page-news__content {
    margin-top: 6.56vw;
    @include mq(md) {
        margin-top: rem(32);
    }
}

.page-news__date {
    font-size: 3.33vw;
    line-height: 1;
    font-weight: $medium;
    letter-spacing: 0.075em;
    @include mq(md) {
        font-size: rem(16);
        line-height: calc(24 / 16);
    }
}

.page-news__tittle {
    font-family: 'serif';
    font-size: 4vw;
    line-height: calc(60 / 30);
    letter-spacing: 0.05em;
    margin-top: 0.5vw;
    font-weight: $regular;
    @include mq(md) {
        font-size: rem(18);
        line-height: calc(30 / 18);
        margin-top: rem(5);
    }
}

.page-news__text p {
    font-family: 'serif';
    font-size: 3.2vw;
    margin-top: 1.08vw;
    line-height: calc(47 / 24);
    font-weight: $regular;
    padding-right: 8.76vw;
    @include mq(md) {
        margin-top: rem(15);
        font-size: rem(12);
        line-height: calc(24 / 12);
        padding-right: initial;
    }
}