@use "global" as *;

.pagination {
    border-top: 0.13vw solid #CBCBCB;
    margin-top: 12.52vw;
    padding-top: 11.83vw;
    @include mq(md) {
        margin-top: 7.44vw;
        padding-top: 6.81vw;
        border-top: rem(1) solid #CBCBCB;
    }
}

.page-numbers {
    display: flex;
    justify-content: center;
    align-items:center;
    gap:0 15.27vw;
    margin-bottom: 14.04vw;
    margin-right: 4.76vw;
    @include mq(md) {
        gap:0 rem(120);
        margin-bottom: rem(131.9);
        margin-right: initial;
    }
}

.page-numbers li .page-numbers {
    font-size: 4vw;
    line-height: calc(30 / 30);
    color:#CBCBCB;
    font-weight: $medium;
    margin-top: initial;
    margin-bottom: initial;
    @include mq(md) {
        font-size: rem(16);
        line-height: calc(24 / 16);
    }
}

.page-numbers li .current {
    color:$black;
}