@use "global" as *;

.inner {
  width: 100%;
  padding-right: $padding-sp;
  padding-left: $padding-sp;
  margin-right: auto;
  margin-left: auto;
  @include mq("md") {
    padding-right: $padding-pc;
    padding-left: $padding-pc;
  }
}

.main-inner {
  max-width: 92.06vw;
  margin-left: auto;
  @include mq(md) {
    max-width: 72.77vw;
  }
}

.page-back-inner {
  height:86.46vw;
  position: relative;
  @include mq(md) {
      height:47.47vw;
  }
}

.page-inner {
  @include mq(md) {
    margin-top: rem(93.7);
  }
}