@use "sass:math";

////////////////////
// インナー幅設定
////////////////////
// コンテンツのインナー幅（最も共通の幅が多い部分）
$inner: 1010px;
// ※キャンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 16px;
// ※PC時：デザインカンプの余白の幅ではない。PC時は通常25pxで大丈夫！


////////////////////
// フォント設定
////////////////////
/// 

@font-face {
  font-family:'accent';
  src: url(../css/fonts/JMH\ Typewriter.otf)format('opentype');
}

@font-face {
  font-family:'american';
  src: url(../css/fonts/AmericanTypewiter.woff)format('woff');
}

@font-face {
  font-family:'base';
  src: url(../css/fonts/NotoSansJP-Regular.otf)format('opentype')
}

@font-face {
  font-family:'serif';
  src:  url(../css/fonts/NotoSerifJP-Regular.otf)format('opentype');
}

html,body {
  font-family: 'base';
}

// フォントウェイト
$regular: 400;
$medium: 500;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$black: #000;
$white: #fff;
$accent:#211815;

////////////////////
// メディアクエリとブレークポイントの設定
////////////////////

//ブレイクポイント
$breakpoints: (
  sm: 600,
  md: 768,
  lg: 1200,
  xl: 1500,
);


//メディアクエリー
$mediaquerys: (
  sm: "screen and (min-width: #{map-get($breakpoints,'sm')}px)",
  md: "screen and (min-width: #{map-get($breakpoints,'md')}px)",
  lg: "screen and (min-width: #{map-get($breakpoints,'lg')}px)",
  xl: "screen and (min-width: #{map-get($breakpoints,'xl')}px)",
);

// @include mq(){}で書くとブレークポイントが反映される（初期値はmd）
@mixin mq($mediaquery: md) {
  @media #{map-get($mediaquerys, $mediaquery)} {
    @content;
  }
}

// remへの計算式（16pxを基準としている。10pxを基準とする場合は16を10に変更する）
@function rem($pixels) {
  @return math.div($pixels, 16) * 1rem;
}


////////////////////
// 表示設定
////////////////////

.u-desktop {
  display: none;
  @include mq("md") {
    display: block;
  }
}

.u-mobile {
  @include mq("md") {
    display: none;
  }
}

.mt-1 {
  margin-top: 14.84vw;
  @include mq(md) {
    margin-top: rem(135);
  }
}

.mt-2 {
  margin-top: 18.75vw;
  @include mq(md) {
    margin-top: rem(110);
  }
}

.mt-3 {
  margin-top: 17.64vw;
  @include mq(md) {
    margin-top: rem(195);
  }
}