@use "global" as *;

.footer {
    background-color: #573B1A;
}

.footer-container {
    padding-top: 6.32vw;
    @include mq(md) {
        padding-top:rem(77);
        padding-bottom: rem(40.3);
    }
}

.footer-wrapper-sp {
    padding-left: 8vw;
    padding-right: 8vw;
    @include mq(md) {
        display: none;
    }
}

.footer-wrapper {
    display: none;
    @include mq(md) {
        display: flex;
        align-items: center;
        justify-content: center;
        padding-left:7.5%;
        padding-right:14%;
    }
}

.footer__text {
    font-size: 3.2vw;
    color:$white;
    font-family: 'serif';
    line-height: calc(47 / 24);
    span {
        display: block;
        font-size: 3.73vw;
        margin-bottom: 0.76vw;
        letter-spacing: 0.075em;
    }
}

.footer__border {
    margin-top: 5.97vw;
    height:0.27vw;
    background-color: #825D33;
    margin-left: -8vw;
    margin-right: -8vw;
}

.footer-nav__content {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    align-items: center;
    margin-top: 11.05vw;
}

.footer-logo {
    width:31.12vw;
    margin-top: 2vw;;
    @include mq(md) {
        width:9.84vw;
        margin-right:4.76vw;
        position: relative;
        margin-top: initial;
    }
    &:after {
        @include mq(md) {
            content:'';
            position: absolute;
            right:-4.85vw;
            width:0.07vw;
            height:4.07vw;
            top:50%;
            transform: translateY(-50%);
            background-color: #825D33;
        }
    }
}

.footer-nav {
    @include mq(md) {
        margin-left:3.44vw;
    }
}

.footer-nav__items {
    @include mq(md) {
        display: flex;
        align-items: center;
    }
}

.footer-nav__item a {
    color: $white;
    font-family: 'serif';
    font-size: 3.73vw;
    display: flex;
    align-items: baseline;
    gap:0 1.12vw;
    @include mq(md) {
        font-size:1.02vw;
        line-height: calc(14 / 14);
        gap:0 0.35vw;
    }
}

.footer-nav__angle {
    width:1.6vw;
    @include mq(md) {
        width:0.44vw;
    }
}

.footer-nav__item:nth-child(n+2) {
    margin-top: 3.93vw;
    @include mq(md) {
        margin-left: rem(37);
        margin-top: initial;
    }
}

.footer__catch {
    color:$white;
    font-family: 'accent';
    font-size: 3.48vw;
    line-height:1.7;
    letter-spacing: -0.035em;
    margin-top: 10.63vw;
    font-weight: $regular;
    margin-right: -3vw;
}

.footer__icon {
    margin-top: 8.01vw;
    width:6.86vw;
    margin-left: auto;
    margin-right: auto;
    @include mq(md) {
        margin-left:1.73vw;
        margin-right: initial;
        margin-top: initial;
        width:1.08vw;
    }
}

.footer__btn {
    margin-top: 9.64vw;
    @include mq(md) {
        margin-left:3.69vw;
        margin-top: initial;
    }
}

.footer-btn {
    position: relative;
    height:9.22vw;
    border-radius: 1.33vw;
    color:$white;
    @include mq(md) {
        font-size:0.88vw;
        line-height: calc(12 / 12);
        border-radius: rem(6);
        height:2.71vw;
        max-width:13.84vw;
    }
}

.footer-angle {
    position:absolute;
    top:50%;
    right:2.01vw;
    transform: translateY(-50%);
    width:1.6vw;
    @include mq(md) {
        position:absolute;
        top:50%;
        right:0.64vw;
        transform: translateY(-50%);
        width:0.44vw;
    }
}

.footer-copyright {
    color:$white;
    text-align: center;
    font-size: 2.67vw;
    line-height: 1;
    letter-spacing: 0.05em;
    margin-top: 2.99vw;
    padding-bottom: 6.75vw;
    @include mq(md) {
        color:$white;
        padding-right: 1.37vw;
        text-align: right;
        font-size: 0.73vw;
        padding-bottom:1.25vw;
        margin-top: initial;
    }
}