@use "global" as *;

.g-nav{
  display: none;
  width:100%;
  transition: all 0.6s;
}

.g-nav-lists {
    width: 100%;
    padding-top:4.37vw;
    border-radius:0 0 rem(10) rem(10);
}

.g-nav-list a {
  border-top: rem(1) solid #CBCBCB;
  padding:4.6vw 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  font-size:4.35vw;
  line-height: calc(24 / 24);
  letter-spacing: 0.075em;
  font-family: 'serif';
  padding-left: 9.43vw;
  padding-right: 8.37vw;
}

.g-nav__angle {
  width:2.63vw;
}

.g-nav-list:last-child a {
  background-color: #603813;
  text-align: center;
  color:$white;
  justify-content: center;
  position: relative;
  border-radius:0 0 rem(10) rem(10);
  font-size:2.93vw;
  line-height: calc(22 / 22);
  .g-nav__angle {
    position: absolute;
    top:50%;
    right:4.53%;
    transform: translateY(-50%);
    width:rem(12);
  }
}