@use "global" as *;

.sable {
    .swiper-container {
        overflow: initial;
    }
}

.top__sable {
    margin-top:32vw;
    @include mq(md) {
        margin-top: rem(93);
    }
}

.sable-wrapper {
    margin-top: 33.37vw;
    @include mq(md) {
        margin-top: rem(90);
    }
}

.sable-wrapper:nth-child(n+2) {
    margin-top:73.31vw;
    @include mq(md) {
        margin-top:6vw;
    }
}

.sable-slide {
    width:100%;
    height:78.61vw;
    img {
        height: 100%;
    }
    @include mq(md) {
        width:rem(994);
        height:43.16vw;
        position: relative;
        margin-top: initial;
        img {
            height: 100%;
            object-fit: cover;
            border-radius: 0 0 0 4.83vw;
        }
    }
}

.sable-slide__wrap {
    @include mq(md) {
        position: absolute;
        top:10vw;
        left:5.1vw;
        width:16.4vw;
    }
}

.sable-slide__number {
    font-family: 'accent';
    position: absolute;
    top:-12.47vw;
    font-size:6.67vw;
    line-height:1.126;
    font-weight: $regular;
    letter-spacing: 0.3em;
    @include mq(md) {
        font-size: rem(40.43);
        line-height: calc(51 / 40.43);
        text-align: center;
        position:initial;
    }
    span {
        font-family: 'american';
    }
}

.sable-slide__text {
    font-family: 'serif';
    margin-top:7vw;
    padding-right:8vw;
    font-size:2.93vw;
    line-height: calc(45 / 22);
    @include mq(md) {
        margin-top:3.66vw;
        font-size: rem(12);
        line-height: calc(27 / 12);
        margin-left: rem(10);
        font-weight: $medium;
        padding-right:initial;
    }
}

.sable-slide__btn {
    font-family: 'serif';
    margin-top:7vw;
    margin-right:8vw;
    @include mq(md) {
        margin-left:0.37vw;
        margin-top: 2.3vw;
    }
}

.sable-pagination {
    display: none;
    @include mq(md) {
        position: absolute;
        top:rem(-30.4);
        right:rem(12);
        display: flex;
    }

    .swiper-pagination-bullet {
            display: none;
            @include mq(md) {
                display: block;
                border:rem(1) solid $black;
                background-color: initial;
                opacity: 1;
                width:rem(8.34);
                height:rem(8.34);
                margin:0 rem(6);
            }
    }

    .swiper-pagination-bullet-active {
        background-color: $black;
    }
}