@use "global" as *;

.top__news {
    margin-top: 20.28vw;
    margin-bottom: 24.77vw;
    @include mq(md) {
        margin-top: rem(95);
        margin-bottom: rem(134);
    }
}

.top-news__wrapper {
    margin-top: 14.96vw;
    @include mq(md) {
        margin-top: initial;
    }
    p {
        color:#392D2A;
        font-weight: $bold;
        @include mq(md) {
            margin-top: rem(90);
            font-size: rem(16);
            line-height: calc(16 / 16);
            border-bottom: rem(0.5) solid #392D2A;
            padding-bottom: rem(18);
        }
    }
}

.top-news__item {
    font-size: 3.33vw;
    font-weight: $bold;
    padding:5vw 0 5.5vw 0;
    border-top: 0.13vw solid #392D2A;
    letter-spacing: 0.1em;
    @include mq(md) {
        padding:rem(16) 0;
        font-size: rem(16);
        line-height: calc(16 / 16);
        border-top:none;
        border-bottom: rem(0.5) solid #392D2A;
    }
    a {
        font-size: 3.33vw;
        font-weight: $bold;
        display: flex;
        align-items: baseline;
        line-height:1;
        gap:0 1.87vw;
        margin-top: 3.69vw;
        letter-spacing:initial;
        @include mq(md) {
            gap:0 rem(6.5);
            margin-top: rem(13.7);
            font-size: rem(15);
            line-height: calc(20 / 15);
            letter-spacing: initial;
        }
    }
    .top-news__angle {
        width:1.6vw;
        @include mq(md) {
            width:0.44vw;
        }
    }
}

.top-news__item:last-child {
    border-bottom: 0.13vw solid #392D2A;
    @include mq(md) {
        border-bottom: rem(0.5) solid #392D2A;
    }
}