@use "global" as *;

.section-tittle {
    color:$accent;
    font-family: 'accent';
    font-weight: $regular;
    font-size:8vw;
    line-height:1.5;
    @include mq(md) {
        font-size: rem(43.92);
        line-height: calc(44 / 43.92);
    }
}

.section-tittle__ja {
    font-family: 'serif';
    color:$black;
    font-size:4.8vw;
    line-height:1;
    display: block;
    @include mq(md) {
        font-size: rem(20);
        line-height: calc(20 / 20);
        margin-top:1vw;
    }
}

.white {
    color:$white;
}