@use "global" as *;

.btn {
    font-family: 'serif';
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    font-size:3.47vw;
    line-height:1;
    letter-spacing: 0.075em;
    color:$black;
    border:rem(1) solid $black;
    height:9.22vw;
    @include mq(md) {
        font-size: 1.02vw;
        line-height: calc(14 / 14);
        width:16.4vw;
        height:2.49vw;
        min-width:9.81vw;
    }
}

.btn-white {
    color:$white;
    border:0.13vw solid $white;
    @include mq(md) {
        border: rem(1) solid $white;
    }
}

.btn-brown {
    color:$white;
    background-color: #603813;
    height:10.67vw;
    width: 100%;
    @include mq(md) {
        height:2.93vw;
        width:26.55vw;
    }
}

.btn-angle {
    position: absolute;
    top:55%;
    transform: translateY(-50%);
    right:2.15vw;
    width:1.6vw;
    @include mq(md) {
        width:0.44vw;
        right:0.5vw;
    }
}